.error-indicator {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #c78f22; }
  .error-indicator img {
    margin-bottom: 1rem; }
  .error-indicator .boom {
    font-size: 1.7rem; }

.spinner-border {
  width: 3rem;
  height: 3rem; }

.random-country {
  display: flex;
  padding: 1rem;
  min-height: 250px; }
  .random-country .container {
    display: flex;
    flex-wrap: nowrap;
    align-items: center; }
  .random-country h4 {
    margin-left: 1rem; }
  .random-country .country-image {
    width: 200px;
    height: auto;
    border-radius: 10px;
    margin-right: 1rem; }
  .random-country .list-group {
    min-width: 300px;
    margin: 1rem 0 0 1rem; }
  .random-country .list-group-item {
    padding: .25rem; }
    .random-country .list-group-item .term {
      margin-right: .5rem; }

.accordion {
  border-bottom: 1px solid rgba(0, 0, 0, 0.125); }

.item-details {
  display: flex;
  flex-direction: row;
  padding: 1rem;
  align-items: center; }
  .item-details .item-image {
    width: 30%;
    border-radius: 10px; }
  .item-details .list-group-item {
    padding: .25rem; }
    .item-details .list-group-item .term {
      margin-right: .5rem; }

.select-country-text {
  width: 100%;
  display: inline-block;
  text-align: center; }

.btn-group {
  width: 100%; }

.form-group {
  margin: 0 auto; }

.heading-text {
  width: 35%;
  margin: 0 auto;
  text-align: center;
  padding-top: 45px;
  font-weight: bold; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

